/* BMI Page Specific Styles */

/* Smooth transitions for filter buttons */
button {
  transition: all 0.2s ease-in-out;
}

/* Card hover effects */
.bg-white:hover,
.dark .dark\:bg-gray-800:hover {
  transform: translateY(-2px);
  transition: transform 0.2s ease-in-out;
}

/* Chart container */
canvas {
  max-height: 320px;
}

/* Category color indicators */
.text-blue-600 {
  color: #2563eb;
}

.text-green-600 {
  color: #16a34a;
}

.text-yellow-600 {
  color: #ca8a04;
}

.text-red-600 {
  color: #dc2626;
}

/* Loading spinner animation */
@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

.animate-spin {
  animation: spin 1s linear infinite;
}

